<template>
  <div class="contact">
    <!-- 聯絡專區 -->
    <div class="row" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp text-center title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >聯絡我們</span
        >
      </div>
    </div>
    <!-- 表單內容 -->
    <div class="contactCard">
      <div>
        <div class="alert alert-warning text-center remind" role="alert">
          如果有任何想給我們的建議
          <br />或想收到住家附近的最新水資訊，都歡迎在下方留言
        </div>

        <div class="col-xl-12 col-12 text-center">
          <div class="row detail">
            <div class="col-xl-6">
              <div class="form-group">
                <label class="classlabel" for="exampleFormControlTextarea1"
                  >名稱 ( Name )</label
                >
                <textarea
                  class="form-control"
                  name="name"
                  rows="1"
                  v-model="form.name"
                ></textarea>
              </div>
              <div class="form-group">
                <label class="classlabel" for="exampleInputEmail1"
                  >*信箱 ( Email )</label
                >
                <input
                  type="email"
                  required
                  class="form-control"
                  name="email"
                  aria-describedby="emailHelp"
                  v-model="form.email"
                />
                <small id="emailHelp" class="form-text text-muted"
                  >*為回覆您的寶貴意見，此格為必填項目</small
                >
              </div>
              <div class="form-group">
                <label class="classlabel" for="exampleFormControlTextarea1"
                  >意見主題 ( Subject )</label
                >
                <textarea
                  class="form-control"
                  name="subject"
                  rows="1"
                  placeholder="Ex: 水庫、降雨..."
                  v-model="form.subject"
                ></textarea>
              </div>
            </div>
            <div class="col-xl-6">
              <div class="form-group">
                <label class="classlabel" for="exampleFormControlTextarea1"
                  >內容 ( Content )</label
                >
                <textarea
                  class="form-control"
                  name="content"
                  rows="10"
                  v-model="form.content"
                ></textarea>
              </div>
            </div>
          </div>
          <button class="btn btn-primary" @click="onSubmit">
            提交 ( Submit )
          </button>
          <span id="askimg2">
            <img src="@/assets/img/question2.png" />
          </span>
        </div>
      </div>
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      form: {
        name: "",
        email: "",
        subject: "",
        content: "",
      },
    };
  },
  methods: {
    onSubmit() {
      if (this.form.email === "") {
        this.$message({
          message: "送出失敗!! 請確認是否填寫信箱",
          type: "error",
        });
        return;
      }
      axios
        .post(`${this.allURL}email`, this.form)
        .then((res) => {
          console.log();
          if (res.data.message === "send!!") {
            this.$message({
              message: "送出成功!! 感謝您的寶貴意見",
              type: "success",
            });
          }
        })
        .catch((errors) => {
          console.log(errors);
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.contact {
  overflow: hidden;
  padding: 2rem 4rem;
  background-color: #e3f0f7;
  font-family: 微軟正黑體;
  font-size: 22px;
  .contactCard {
    background-color: #fff;
    padding: 2% 3% 3% 3%;
    border-radius: 15px;
    .remind {
      font-size: 28px;
      font-family: 微軟正黑體;
      letter-spacing: 5px;
    }
    .detail {
      padding: 2%;
    }
  }
  .footer {
    background-color: #e3f0f7;
    height: 100px;
  }
  .title {
    margin: 0 auto 3% auto;
    border-color: #dcdcdc;
    border-width: 2px;
    padding: 1%;
    display: inline-block;
    line-height: 60px;
    letter-spacing: 21px;
    text-decoration: none;
    font-weight: 600;
    outline: 0;
    font-size: 2.5em;
  }
  .none {
    width: 100%;
    padding-top: 76px;
  }
  .form-control {
    font-size: 24px;
    color: #000;
  }
  .text-muted {
    font-size: 20px;
    font-weight: bold;
  }
  .classlabel {
    font-size: 28px;
    color: #fff;
    background: #064780;
    border-radius: 10px;
    padding: 5px 15px;
    margin-bottom: 10px;
    margin: 2% 0px;
  }
  .col-xl-6 {
    padding: 0 1%;
  }
  .btn {
    width: 220px;
    font-size: 24px;
    line-height: 24px;
    padding: 20px;
  }
  #askimg2 img {
    max-width: 15%;
    height: inherit;
    overflow: hidden;
    position: absolute;
    bottom: 0px;
    right: -1%;
    bottom: -40px;
    opacity: 0.3;
  }
  @media (max-width: 450px) {
    padding: 2% 4% !important;
    font-size: 14px;
    .title {
      padding: 0px 15px;
      letter-spacing: 5px;
      font-size: 26px;
      line-height: 50px;
    }
    .footer {
      height: 0;
    }
    .contactCard {
      margin: 0;
    }
    .classlabel {
      font-size: 16px;
    }
    .contactCard {
      padding: 2%;
      .remind {
        font-size: 14px;
        letter-spacing: 2px;
        margin: 2% 2% 0 2%;
      }
    }
    .text-muted {
      font-size: 12px;
    }
    .form-control {
      font-size: 14px;
      line-height: 14px;
    }
    #askimg2 img {
      max-width: 20%;
      position: absolute;
      bottom: 0px;
      right: 3%;
      bottom: -5px;
    }
    .btn {
      width: 120px;
      font-size: 14px;
      line-height: 14px;
      padding: 2% 1%;
    }
  }
}
</style>